import { Component, OnInit } from '@angular/core';
import {User} from '../add-user/user';
import {UserService} from '../add-user/user.service';

@Component({
  selector: 'app-updatebal',
  templateUrl: './updatebal.component.html',
  styleUrls: ['./updatebal.component.css']
})
export class UpdatebalComponent{

  user: User;
  submitted:Boolean;

  constructor(

    private userService: UserService) {
      this.submitted=false;
this.user = new User();
}

onSubmit() {
this.submitted=true;
this.userService.update(this.user.accountno,this.user).subscribe(result => console.log("Updated"));
}

}
